import axios from 'axios'
import { Message } from 'element-ui'
//  Nprogress 
import NProgress from 'nprogress'

// 
// let baseURL = "https://music-cloud-fpgfem8aa-aliveleqi.vercel.app/"
// let baseURL = "http://124.223.89.194:3000/"
// let baseURL = "http://localhost:3000"
// let baseURL = "https://lianghj.top:3000"
// let baseURL = 'http://duoduozuikeail.top:3000'


// let baseURL = 'https://lianghj.top:3000'
let baseURL = "https://music-cloud-fpgfem8aa-aliveleqi.vercel.app/"

const service = axios.create({
    baseURL,
    timeout: 5000,
    changeOrigin: true,  //
    withCredentials: true, //axios cookie
})

// 
//     NProgress.start()
service.interceptors.request.use(config => {
    //  
    NProgress.start()

    return config
})

// 
service.interceptors.response.use(config => {
    //  
    NProgress.done()

    return config
})

service.interceptors.response.use(
    response => {
        if (response.status == 200) {
            return Promise.resolve(response)
        } else {
            return Promise.reject(response)
        }
    },
    error => {
        error && Message({
            type: 'error',
            message: '~ ~ ',
            showClose: true
        })
    }
)

export default service