<template>
  <div class="header">
    <!-- <script src="https://apps.bdimg.com/libs/jquery/2.1.4/jquery.min.js"></script> -->
    <!--  -->
    <div class="header-title">
      <h2 @click="gohome">By Alive</h2>
    </div>
    <!--  -->
    <div class="btns">
      <!--  -->
      <i class="el-icon-arrow-left" @click="go(-1)"></i>
      <!--  -->
      <i class="el-icon-arrow-right" @click="go(1)"></i>
      <!--  -->
      <i class="el-icon-picture-outline" id="btbg" @click=""></i>
    </div>

    <div>
      <!--  -->
      <div class="input-box">
        <el-input
          v-model="searchValue"
          :placeholder="placeholder"
          prefix-icon="el-icon-search"
          @keyup.enter.native="search"
          @focus="getSearchData"
          @blur="outblur"
        >
        </el-input>
      </div>
      <!--  -->
      <!-- @mousedown.event.preventDefault();blur -->
      <transition name="el-fade-in-linear">
        <div class="search-hot" v-show="showHot" ref="hot">
          <!--  element-UI  <el-scrollbar> -->
          <el-scrollbar style="height: 100%">
            <div class="history" v-if="history.length">
              <span
                class="hot-title"
                style="display: inline-block; margin-right: 5px"
                ></span
              >
              <span
                class="iconfont icon-lajitong"
                style="cursor: pointer"
                title=""
                @mousedown="deleteHistory(0, true)"
              ></span>
              <div class="history-wrap">
                <div
                  class="history-item"
                  v-for="(item, index) in history"
                  :key="index"
                  @mousedown="toHot(item)"
                >
                  {{ item }}
                  <span
                    class="delete-btn"
                    title=""
                    @mousedown.stop="deleteHistory(index, false)"
                    ></span
                  >
                </div>
              </div>
            </div>
    
            <div class="hot-title"></div>
            <ul>
              <li
                class="hot-item"
                v-for="(item, index) in hotData"
                :key="index"
                @mousedown="toHot(item.searchWord)"
              >
                <!--  -->
                <div class="hot-index">{{ index + 1 }}</div>
                <!--  -->
                <div class="hot-info">
                  <div class="hot-top">
                    <!--  -->
                    <div class="hot-name">{{ item.searchWord }}</div>
                    <div class="hot-score">{{ item.score }}</div>
                    <img
                      class="hot-icon"
                      v-show="item.iconUrl && item.iconType != 5"
                      :src="item.iconUrl"
                    />
                  </div>
                  <div class="hot-content">{{ item.content }}</div>
                </div>
              </li>
            </ul>
          </el-scrollbar>
        </div>
      </transition>

      <transition name="el-fade-in-linear">
        <div class="search-hot" v-show="isAdvice" >
          <el-scrollbar style="height: 100%">
            <ul>
              <li
                class="hot-item"
                v-for="(item, index) in suggestionList.songs"
                :key="index"
                @mousedown="toHot(item.name)"
              >
                <!--  -->
                <div class="hot-info">
                  <div class="hot-top">
                    <!--  -->
                    <div class="hot-name">{{ item.name }}</div>
                  </div>
                </div>
              </li>
            </ul>
          </el-scrollbar>
        </div>
      </transition>
    </div>
 
    <div class="sliderBar">
      <el-slider v-model="sliderValue" :format-tooltip="formatTooltip" ></el-slider>
    </div>

    <div class="userInfo-wrap">
      <div class="avater">
        <span v-show="!isLogin" @click="toLogin()"></span>
        <img  v-show="isLogin" :src="userInfo.avater" @click="toLogin()" >
      </div>
      <div class="user-wrap">
        <span>{{ userInfo.username }}</span>
        <i class="el-icon-caret-bottom"></i>
      </div>
    </div>
  
  </div>
</template>

<style>
</style>

<script>
// axios
import axios from "axios";
import { searchHintAPI,searchSuggestAPI,hotSearchAPI } from '@/utils/api'
import { getBgAPI } from '@/utils/api_2'
import { mapState } from 'vuex'
export default {
  name: "top",
  data() {
    return {
      // 
      searchValue: "",
      // 
      showHot: false,
      // 
      hotData: [],
      // 
      history: [],
      // 
      placeholder: "",
      // 
      isAdvice: false,
      // 
      suggestionList: [],
      // 
      bg:'',
      // 
      sliderValue: 35,
      // 
      // isFocus: false,
    };
  },
  computed:{
    ...mapState(['userInfo','isLogin','token'])
  },
  created() {
    // 
    this.getDefaultKeywords();
  },

  watch: {
    searchValue() {
      this.getSuggestion();
    },
    sliderValue(){
      this.changeOpacity();
    }
  },

  methods: {
    gohome(){
      if (this.$route.path != '/discovery') {
        this.$router.push('/discovery')
      }
    },
    // router
    go(n) {
      this.$router.go(n);
    },

    toLogin(){
      if(this.isLogin || this.token || localStorage.getItem('token')){
        this.$router.push(`/userInfo`);
      }else{
        this.$router.push(`/login`);
      }
    },

    // 
    changeBg(){
        getBgAPI()
        .then(res => {
          console.log('',res)
          this.bg = res.data.pic
          let style = document.createElement(`style`)
          let text = document.createTextNode(`body::after{background-image: url(${this.bg});}`);    // 
          style.appendChild(text)
          document.body.appendChild(style)})
    },

    // 
    changeOpacity(){
      // console.log(':',this.sliderValue/100)
      let style = document.createElement(`style`)
      let text = document.createTextNode(`body::after{opacity: ${this.sliderValue/100};}`);    // 
      style.appendChild(text)
      document.body.appendChild(style)
    },

    prevent() {
      event.preventDefault();
    },

    showError() {
      this.$message({
        showClose: true,
        message: "",
        type: "error",
      });
    },
    //
    isNull(str) {
      if (str == "") return true;
      var regu = "^[ ]+$";
      var re = new RegExp(regu);
      return re.test(str);
    },

    outblur(){
      this.isAdvice = false
      this.showHot = false
    },

    // 
    search() {
      if (this.isNull(this.searchValue)) this.showError();
      else {
        // push
        this.$router.push(`/result?q=${this.searchValue}`);
        if (!this.history.includes(this.searchValue))
          this.history.push(this.searchValue);
        this.outblur()
      }
    },

    // 
    getDefaultKeywords() {
      searchHintAPI().then((res) => {
        // console.log(':',res)
        // 
        this.placeholder = res.data.data.showKeyword;
      });
    },

    // 
    getSuggestion() {
      this.isAdvice = true;
      let params ={
              keywords: this.searchValue,
              // 
            }
      if (this.searchValue != "") {
            searchSuggestAPI(params).then((res) => {
            // console.log(":", res);
            // 
            this.suggestionList = res.data.result;
            // el-input
          });
      }else{
        this.isAdvice = false;
      }
    },

    // 
    getSearchData() {
      if (this.searchValue != "") {
        this.getSuggestion();
      } else {
        this.showHot = true;
        if (this.hotData.length == 0) {
          hotSearchAPI().then((res) => {
            // console.log(":", res);
            // 
            this.hotData = res.data.data;
          });
        }
      }
    },

    // 
    toHot(value) {{
        this.searchValue = value;
        this.search();
        //blur @mousedown.prevent; mousedownblur
        // event.preventDefault();
      }
    },

    // 
    deleteHistory(index, all) {
      // 
      if (all) this.history = [];
      // historyindex
      else
        for (let i = 0; i < this.history.length; i++) {
          if (i == index)
            // array.splice( start, deleteCount [,items... ] )splice()startdeleteCount
            this.history.splice(i, 1);
        }
      // x.prevent
      event.preventDefault();
    },
    // 
    formatTooltip(val) {
        return val / 100;
      }
  },
};
</script>