export const getType = obj => {
	let str = Object.prototype.toString.call(obj);
	return str
		.substr(1, str.length - 2)
		.split(" ")[1]
		.toLowerCase();
};
export function isTel(s) {
	var reg = /^(\d{3,4})?\d{7,8}$/;
	if (!reg.test(s)) {
		return false;
	}
	return true;
}
export function isPhone(s) {
	var reg = /^1[356789]\d{9}$/;
	if (!reg.test(s)) {
		return false;
	}
	return true;
}
export const isArray = obj => Object.prototype.toString.call(obj) === "[object Array]";
export const hideTel = tel => tel.replace(/(\d{3})(\d{4})(\d{4})/, "$1****$3");
export const pwdReg = /^(?![A-z0-9]+$)(?=.[^%&',;=?$\x22])(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,20}$/;

export const isEmptyObj = obj => {
	for (let _ in obj) {
		return 0;
	}
	return 1;
};
export const getRandom = (min, max) => {
	return Math.floor(Math.random() * (max - min + 1)) + min;
};
/**
 * 
 localStorage
 */
export const setItem = (key, value) => {
	if (typeof value === "object") {
		value = JSON.stringify(value);
	}
	window.localStorage.setItem(key, value);
};
export const getItem = key => {
	const data = window.localStorage.getItem(key);
	try {
		return JSON.parse(data);
	} catch (err) {
		return data;
	}
};
export const removeItem = key => {
	window.localStorage.removeItem(key);
};
/**
 *  a=1&b=2
 * @param obj 
 */
export function obj2String(obj, arr = [], idx = 0) {
	for (let item in obj) {
		arr[idx++] = [item, obj[item]];
	}
	return new URLSearchParams(arr).toString();
}

/**
 * 
 * @param date    Date
 * @param fmt   yyyy-MM-dd HH:mm:ss
 * @returns {*}   "2018-09-06 10:15:59"
 * @constructor
 */
export const Dateformat = (date, fmt) => {
	var o = {
		"M+": date.getMonth() + 1, //
		"d+": date.getDate(), //
		"h+": date.getHours() % 12 == 0 ? 12 : date.getHours() % 12, //
		"H+": date.getHours(), //
		"m+": date.getMinutes(), //
		"s+": date.getSeconds(), //
		"q+": Math.floor((date.getMonth() + 3) / 3), //
		S: date.getMilliseconds(), //
	};
	if (/(y+)/.test(fmt)) {
		fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
	}
	for (var k in o) {
		if (new RegExp("(" + k + ")").test(fmt)) {
			fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
		}
	}
	return fmt;
};

/**
 * html
 * @param htmlcontent
 * @return
 */
export const filterText = str => {
	if (!str) return str;
	str = str.replace(/<\/?[^>]*>/g, ""); // HTML tag
	str = str.replace(/[ | ]*\n/g, "\n"); // 
	str = str.replace(/ /gi, ""); // 
	str = str.replace(/&nbsp;/g, ""); // 
	return str;
};

export function debounce(callback, time) {
	// 
	let timer = null;
	// 
	return function(e) {
		// 1 
		// 2 
		// null
		if (timer !== null) {
			// 
			clearTimeout(timer);
		}
		// 
		timer = setTimeout(() => {
			// 
			callback.call(this, e);
			// null
			timer = null;
		}, time);
	};
}

export function throttle(callback, wait) {
	// 
	let start = 0;
	// 
	return function(e) {
		// 
		let now = Date.now();
		if (now - start >= wait) {
			//  
			callback.call(this, e);
			start = now;
		}
	};
}

// 
export function isFullscreen() {
	var fullscreenEle = document.fullscreenElement || document.mozFullScreenElement || document.webkitFullscreenElement;
	return fullscreenEle;
}
/** */
export function requestFullScreen() {
	const docElm = document.documentElement;
	if (docElm.requestFullscreen) {
		docElm.requestFullscreen();
	} else if (docElm.msRequestFullscreen) {
		docElm.msRequestFullscreen();
	} else if (docElm.mozRequestFullScreen) {
		docElm.mozRequestFullScreen();
	} else if (docElm.webkitRequestFullScreen) {
		docElm.webkitRequestFullScreen();
	}
}
/** */
export function exitFullscreen() {
	const de = window.parent.document;

	if (de.exitFullscreen) {
		de.exitFullscreen();
	} else if (de.mozCancelFullScreen) {
		de.mozCancelFullScreen();
	} else if (de.webkitCancelFullScreen) {
		de.webkitCancelFullScreen();
	} else if (de.msExitFullscreen) {
		de.msExitFullscreen();
	}
}
